.App {
  overflow: hidden;
  text-align: center;
  background-color: #343541;
}

.App-logo {
  height: 72px;
  pointer-events: none;
}

.HelpText {
  font-family: sans-serif;
  margin-top: 10px;
  font-size: 25px;
}

/*@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}*/

.TextBox {
  border-radius: 5px;
}

.Footer {
  display: flex;
  width: 100vw;
  margin-top: auto;
  justify-content: center;
  align-items: center;
  min-height: 15vh;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  min-height: 85vh;
}

.TextBox {
  width: 90vw;
  height: 6vh;
  max-height: 50px;
  border-radius: 20px;
  background-color: #343541;
  border-color: #9899ad;
  resize: none;
  font-family: sans-serif;
  color: #f7f5ff;
  text-wrap: wrap;
  padding-left: 2vw;
}

textarea::placeholder {
    color: #f7f5ff;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
